import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import './Join.scss'; //  

import { useCallback } from "react";

const Join = () => {
    const [formData, setFormData] = useState({
        userid: "",
        username: "",
        password: "",
        repassword: "",
        email: "",
        birth: "",
        gender: "",
        profileImage: "/ProfileImg/anonymous.jpg",
        authCode: "",
    });

    const [imagePreview, setImagePreview] = useState("/ProfileImg/anonymous.jpg"); //  
    const [validationMessages, setValidationMessages] = useState({});
    const [authCodeSent, setAuthCodeSent] = useState(false);
    const [timeSee,setTimeSee] =useState(false);
    const [timer, setTimer] = useState(180);
    const [isAuthCodeVerified, setIsAuthCodeVerified] = useState(false);
    const [isFirstSend, setIsFirstSend] = useState(true); //   true

    const [formImgeData, setFormImgeData] = useState({
        profileImage: null, //   
    });

    

    // 
    useEffect(() => {
        let countdown;
        if (authCodeSent && timer > 0 && !isAuthCodeVerified) {
            countdown = setInterval(() => setTimer((prev) => prev - 1), 1000);
        }
        return () => clearInterval(countdown);
    }, [authCodeSent, timer, isAuthCodeVerified]);


    //    
    // const { password, repassword } = formData;

    const validatePasswords = useCallback(() => {
        if (formData.password && formData.repassword) {
            const isValid = formData.password === formData.repassword;
            const message = isValid ? " ." : "  .";
            const color = isValid ? "validation-success" : "validation-error";
    
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                repassword: message,
                repasswordColor: color, //   
            }));
        } else {
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                repassword: "",
                repasswordColor: "",
            }));
        }
    }, [formData.password, formData.repassword]);
    
    

    //    useEffect 
    useEffect(() => {
        validatePasswords();
    }, [validatePasswords]);


    //    (email & password)
    const validateBirth = (birth) => {
        const today = new Date();
        const regex = /^\d{8}$/;
    
        if (!regex.test(birth)) {
            return { message: " YYYYMMDD   8 .", color: "validation-error" };
        }
    
        const year = parseInt(birth.substring(0, 4), 10);
        const month = parseInt(birth.substring(4, 6), 10);
        const day = parseInt(birth.substring(6, 8), 10);
    
        if (year < 1900 || year > today.getFullYear()) {
            return { message: "  1900  .", color: "validation-error" };
        }
    
        if (month < 1 || month > 12) {
            return { message: " 1~12  .", color: "validation-error" };
        }
    
        const daysInMonth = new Date(year, month, 0).getDate();
        if (day < 1 || day > daysInMonth) {
            return { message: ` ${month}   (${daysInMonth})  .`, color: "validation-error" };
        }
    
        const inputDate = new Date(year, month - 1, day);
        if (inputDate > today) {
            return { message: "     .", color: "validation-error" };
        }
    
        return { message: "", color: "validation-success" }; //  
    };

    
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    
     
    
        if (name === "email") {
            const { message, color } = checkEmail(value);
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                email: message,
                emailColor: color,
            }));
        }
    
        if (name === "password") {
            const { message, color } = validatePassword(value);
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                password: message,
                passwordColor: color,
            }));
    
            //   ,     
            validatePasswords();
        }
    
        if (name === "repassword") {
            validatePasswords(); //        
        }
    
        if (name === "birth") {
            const { message, color } = validateBirth(value);
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                birth: message,
                birthColor: color,
            }));
        }
    
        if (name === "username") {
            const usernameRegex = /^[a-zA-Z-]+$/; // ,  
            if (!usernameRegex.test(value)) {
                setValidationMessages((prevMessages) => ({
                    ...prevMessages,
                    username: "     .",
                    usernameColor: "validation-error",
                }));
                setFormData((prevData) => ({
                    ...prevData,
                    username: "",
                }));
                return;
            } else {
                setValidationMessages((prevMessages) => ({
                    ...prevMessages,
                    username: "",
                    usernameColor: "",
                }));
            }
        }
    };
    
    
    

        //     ,   
        // Ref 
        const fileInputRef = useRef(null);
        

        const handleImageUpload = (file) => {
            if (file) {
                setFormData({ ...formData, profileImage: file });
                const reader = new FileReader();
                reader.onload = (event) => setImagePreview(event.target.result);
                reader.readAsDataURL(file);
            }
        };
    
        const handleFileInputClick = () => {
            if (fileInputRef.current) {
                fileInputRef.current.click(); //  
            }
        };
    
        const handleDrop = (e) => {
            e.preventDefault();
            if (e.dataTransfer.files.length > 0) {
                handleImageUpload(e.dataTransfer.files[0]);
            }
        };
        
        //  
        const handleCancelImage = () => {
            setFormImgeData({ ...formImgeData, profileImage: null }); //   
            setImagePreview("/ProfileImg/anonymous.jpg"); //    
            setFormData({ ...formData, profileImage: "/ProfileImg/anonymous.jpg" }); // formData profileImage   
        };

        const handleDragOver = (e) => e.preventDefault();

    
    // password   
    const validatePassword = (password) => {
        const passwordRegex = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d@$!%*?&]{8,15}$/;
        if (!password) {
            return { message: " .", color: "validation-error" };
        }
    
        if (/\s/.test(password)) {
            return { message: "    .", color: "validation-error" };
        }
    
        if (!passwordRegex.test(password)) {
            return { message: " + , 8~15 .", color: "validation-error" };
        }
    
        return { message: " .", color: "validation-success" };
    };



   // ID   
    const checkUserId = async (userid) => {
        try {
            //  
            if (!/^[a-zA-Z0-9]{4,12}$/.test(userid)) {
                setValidationMessages((prev) => ({
                    ...prev,
                    userid: "ID 4~12    .",
                    useridColor: "error",
                }));
                return;
            }

            //   
            const response = await axios.post("https://www.tripplannerbn.shop/user/check-id", { userid });
            setValidationMessages((prev) => ({
                ...prev,
                userid: response.data.available ? "  ID." : "   ID.",
                useridColor: response.data.available ? "success" : "error",
            }));
        } catch (error) {
            console.error("ID     :", error);
            setValidationMessages((prev) => ({
                ...prev,
                userid: "  ID   .",
                useridColor: "error",
            }));
        }
    };



    // email   
    const checkEmail = async (email) => {
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
            setValidationMessages((prev) => ({
                ...prev,
                email: "   .",
                emailColor: "validation-error", //  
            }));
            return false; //   
        }
    
        try {
            //    
            const response = await axios.post("https://www.tripplannerbn.shop/user/check-email", { email });
    
            setValidationMessages((prev) => ({
                ...prev,
                email: response.data.available
                    ? "  ."
                    : "  .",
                emailColor: response.data.available ? "validation-success" : "validation-error",
            }));
    
            return response.data.available; //     
        } catch (error) {
            console.error("  :", error);
            setValidationMessages((prev) => ({
                ...prev,
                email: "     .",
                emailColor: "validation-error", //  
            }));
    
            return false; //  
        }
    };

    useEffect(() => {
        let countdown;
        if (timeSee && timer > 0 && !isAuthCodeVerified) {
            countdown = setInterval(() => setTimer((prev) => prev - 1), 1000);
        }
    
        //  0     
        if (timer === 0) {
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                email: " .  .",
            }));
            setAuthCodeSent(false);
        }
    
        return () => clearInterval(countdown);
    }, [timeSee, timer, isAuthCodeVerified]);


    const resetAuthState = () => {
        setAuthCodeSent(false); //     
        setTimer(180); //  
        setIsAuthCodeVerified(false); //   
        setFormData((prev) => ({ ...prev, authCode: "" })); //    
        setValidationMessages((prevMessages) => ({
            ...prevMessages,
            authCode: "",
        }));
    };



    //     
    const sendAuthCode = async () => {
        const email = formData.email.trim(); //   
    
        //    
        if (!email) {
            setValidationMessages((prev) => ({
                ...prev,
                email: " .",
                emailColor: "validation-error",
            }));
            setAuthCodeSent(false); //    
            return;
        }
    
        //     
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
            setValidationMessages((prev) => ({
                ...prev,
                email: "   .",
                emailColor: "validation-error",
            }));
            setAuthCodeSent(false); //    
            return;
        }
    
        try {
            const response = await axios.post("https://www.tripplannerbn.shop/user/send-auth-code", {
                email: email,
            });
    
            if (response.data.status === "success") {
                resetAuthState(); //  
                const message = isFirstSend
                    ? " ."
                    : " .";
                setValidationMessages((prev) => ({
                    ...prev,
                    email: message,
                    emailColor: "validation-success",
                }));
                setAuthCodeSent(true); //    
                setIsFirstSend(false); //     
            } else {
                setValidationMessages((prev) => ({
                    ...prev,
                    email: response.data.message || "   .",
                    emailColor: "validation-error",
                }));
                setAuthCodeSent(false); //    
            }
        } catch (error) {
            setValidationMessages((prev) => ({
                ...prev,
                email: "      .",
                emailColor: "validation-error",
            }));
            setAuthCodeSent(false); //    
        }
    };
    
    
    
    
    
    

    const verifyAuthCode = async () => {
        try {
            const response = await axios.post("https://www.tripplannerbn.shop/user/verify-auth-code", {
                email: formData.email,
                code: formData.authCode,
            });
    
            if (response.data.message.includes("")) {
                setIsAuthCodeVerified(true);
                setValidationMessages((prevMessages) => ({
                    ...prevMessages,
                    authCode: " .",
                    authCodeColor: "validation-success", //  
                }));
            } else {
                setValidationMessages((prevMessages) => ({
                    ...prevMessages,
                    authCode: "  .",
                    authCodeColor: "validation-error", //  
                }));
            }
        } catch (error) {
            setValidationMessages((prevMessages) => ({
                ...prevMessages,
                authCode: "     .",
                authCodeColor: "validation-error", //  
            }));
        }
    };


    // Form  
    const handleSubmit = async (e) => {
        e.preventDefault();
        
        //    
        const {
            userid,
            username, // username 
            password,
            repassword,
            email,
            birth,
            gender,
        } = formData;
    
        if (!userid || validationMessages.useridColor === "error") {
            alert("ID  .");
            return;
        }
    
        if (!username || validationMessages.usernameColor === "validation-error") {
            alert("  .");
            return;
        }
    
        if (!email || validationMessages.emailColor === "error") {
            alert("  .");
            return;
        }
    
        if (!password || validationMessages.passwordColor === "error") {
            alert("  .");
            return;
        }
    
        if (!repassword || password !== repassword) {
            alert("   .");
            return;
        }
    
        if (!birth || validationMessages.birthColor === "validation-error") {
            alert("  .");
            return;
        }
    
        if (!gender) {
            alert(" .");
            return;
        }
    
        //      
        const data = new FormData();
        Object.keys(formData).forEach((key) => {
            if (key !== "profileImage") data.append(key, formData[key]);
        });
    
        // profileImage username 
        data.append("profileImage", formData.profileImage || "/ProfileImg/anonymous.jpg");
        
    
        try {
            const response = await axios.post("https://www.tripplannerbn.shop/user/join", data);
            if (response.status === 200) {
                alert(" .");
            }
        } catch (error) {
            alert("   .  .");
        }
    };
    


    return (
        <div className="all">
            <h1></h1>
            <form onSubmit={handleSubmit}>
                <div className="image-preview-container" onDrop={handleDrop} onDragOver={handleDragOver}>
                    <img src={imagePreview} alt="" className="circle-preview" />
                </div>
                <div id="imgbutton">
                    <button
                        id="imgupload"
                        type="button"
                        onClick={handleFileInputClick}
                    >
                         
                    </button>
                    {formData.profileImage && (
                        <button
                            id="imgcancel"
                            type="button"
                            onClick={handleCancelImage}
                            style={{ marginLeft: "10px", color: "red" }}
                        >
                             
                        </button>
                    )}
                    <input
                        ref={fileInputRef}
                        id="fileInput"
                        type="file"
                        accept="image/*"
                        style={{ display: "none" }}
                        onChange={(e) => handleImageUpload(e.target.files[0])}
                    />
                </div>

                <input
    type="text"
    name="userid"
    placeholder="ID"
    onChange={(e) => {
        handleChange(e);
        checkUserId(e.target.value);
    }}
/>
{validationMessages.userid && (
    <div
        className={`validation-message ${
            validationMessages.useridColor === "success"
                ? "validation-success"
                : "validation-error"
        }`}
    >
        {validationMessages.userid}
    </div>
)}


                <input
                    type="text"
                    name="username"
                    placeholder=""
                    onChange={handleChange}
                />
                {validationMessages.username && (
                    <div className={`validation-message ${validationMessages.usernameColor}`}>
                        {validationMessages.username}
                    </div>
                )}

                {/*  */}
                <div
    className={`emailbox 
        ${validationMessages.email ? 'has-message' : ''} 
        ${validationMessages.username ? 'name-message' : ''}`}
    >
    <div className="email-wrapper">
        <input
            type="email"
            name="email"
            placeholder=" "
            onChange={handleChange}
            onBlur={(e) => checkEmail(e.target.value)}
        />
        <button
            className="emailbutton"
            type="button"
            onClick={() => {
                if (validationMessages.emailColor === "validation-success") {
                    sendAuthCode();
                } else {
                    alert("   .");
                }
            }}
        >
              
        </button>
    </div>
    {/*    */}
    {validationMessages.email && (
        <div className={`validation-message ${validationMessages.emailColor}`} >
            {validationMessages.email}
        </div>
    )}
</div>

{authCodeSent && validationMessages.emailColor === "validation-success" && (
    <div className={`auth-codebox ${validationMessages.authCode ? 'auth-message' : ''}`}>
        <div className="auth-code-wrapper">
            <input
                type="text"
                name="authCode"
                className="auth-code"
                placeholder="  "
                onChange={handleChange}
            />
            <div id="timer">
                {Math.floor(timer / 60)}:{timer % 60}
            </div>
            <button className="verifybutton" type="button" onClick={verifyAuthCode}>
                  
            </button>
        </div>
        {validationMessages.authCode && (
            <div className={`validation-message ${validationMessages.authCodeColor}`}>
                {validationMessages.authCode}
            </div>
        )}
    </div>
)}


                <input type="password" name="password" placeholder="" onChange={handleChange} />
                {validationMessages.password && (
                    <div className={`validation-message ${validationMessages.passwordColor}`}>
                        {validationMessages.password}
                    </div>
                )}

                <input type="password" name="repassword" placeholder=" " onChange={handleChange} />
                {validationMessages.repassword && (
                    <div className={`validation-message ${validationMessages.repasswordColor}`}>
                        {validationMessages.repassword}
                    </div>
                )}

                <input
                    type="text"
                    name="birth"
                    placeholder="(YYYYMMDD)"
                    maxLength={8}
                    onChange={handleChange}
                />
                {validationMessages.birth && (
                    <div className={`validation-message ${validationMessages.birthColor}`}>
                        {validationMessages.birth}
                    </div>
                )}

                <div>
                    <label>
                        <input type="radio" name="gender" value="man" onChange={handleChange} /> 
                    </label>
                    <label>
                        <input type="radio" name="gender" value="woman" onChange={handleChange} /> 
                    </label>
                </div>
                {validationMessages.gender && (
                    <div className="validation-message" >{validationMessages.gender}</div>
                )}
                <button type="submit">  </button>


            </form>


            
        </div>

        

    );
};

export default Join;
