
import { Link } from 'react-router-dom';
import './Main.scss';
import { useEffect, useState } from 'react';
import axios from 'axios';

const Main = () => {
    const [totalPlans,setTotalPlans] = useState(0); //   

    useEffect(()=>{
        axios.get('https://www.tripplannerbn.shop/planner/plans/total')
        .then((response)=>{
            setTotalPlans(response.data); //    
        })
        .catch((error)=>{
            console.error("   ",error);
        });
    },[]);

    return (
        <div className="main-wrapper">

            <h2 className="main-title">
                 ?  <br />
                <br />

                  !

            </h2>

            <Link to="/makePlanner">
                <button className="main-makeplanner-btn">  </button>
            </Link>
            <Link to="/planner/board">
                <button className="main-board-btn">   </button>
            </Link>

            {/*     */}
            <div className='total-plans-display'>
                    : <strong>{totalPlans}</strong>
            </div>

        </div>
    )

}

export default Main