import React, { useState, useEffect } from "react";
import { DateRangePicker } from "react-date-range";
import "react-date-range/dist/styles.css"; //  
import "react-date-range/dist/theme/default.css"; //  
import "./PlannerDate.scss";

const PlannerDate = (props) => {
  const [state, setState] = useState([
    {
      startDate: new Date(),
      endDate: new Date(),
      key: "selection",
    },
  ]);
  const [showCalendar, setShowCalendar] = useState(false); //   
  const [inputValue, setInputValue] = useState(""); //    
  const [area, setArea] = useState([126.9779692, 37.566535]); //   
  const [areaName, setAreaName] = useState("");

  const today = new Date(); //   
  today.setHours(0, 0, 0, 0); //  

  const areaCoordinate = [
    { value: [0, 0], name: " " },
    { value: [126.9779692, 37.566535], name: "" },
    { value: [129.05562775, 35.1379222], name: "" },
    { value: [126.7052062, 37.4562557], name: "" },
    { value: [128.601445, 35.8714354], name: "" },
    { value: [127.3845475, 36.3504119], name: "" },
    { value: [126.8526012, 35.1595454], name: "" },
    { value: [129.3113596, 35.5383773], name: "" },
    { value: [127.5183, 37.4138], name: "" },
    { value: [128.18161, 37.142803], name: "" },
    { value: [127.1516, 36.8075], name: "" },
    { value: [126.991, 34.8679], name: "" },
    { value: [127.153, 35.7175], name: "" },
    { value: [128.8889, 36.4919], name: "" },
    { value: [128.2132, 35.4606], name: "" },
    { value: [128.1555, 37.8228], name: "" },
    { value: [126.794586, 33.532237], name: "" },
  ];

  useEffect(() => {
    setShowCalendar(true); //       
  }, []);

  const handleDateChange = (ranges) => {
    const startDate = ranges.selection.startDate;
    const endDate = ranges.selection.endDate;
    const diffDays = Math.ceil(
      (endDate - startDate) / (1000 * 60 * 60 * 24)
    );

    if (startDate < today || endDate < today) {
      alert("     .");
      return;
    }

    if (diffDays > 10) {
      alert("  10  .");
      return;
    }

    setState([ranges.selection]);
  };

  const handleSelect = (e) => {
    const data = e.target.value;
    if (data != null) {
      const selectedArea = areaCoordinate.find(
        (item) => item.value.toString() === data.toString()
      ).name;

      setArea(data.split(","));
      setAreaName(selectedArea);
    }
  };

  const handleConfirm = () => {
    const startDate = state[0].startDate;
    const endDate = state[0].endDate;

    const formattedDate = `${startDate.toLocaleDateString()} ~ ${endDate.toLocaleDateString()}`;
    setInputValue(formattedDate);
    setShowCalendar(false); //  
  };

  const handleNext = async () => {
    const startDate = state[0].startDate;
    const endDate = state[0].endDate;

    if (!area || area === " ") {
      alert(" .");
      return;
    }
    //   
    props.DayData(
      (endDate.getTime() - startDate.getTime()) / (1000 * 3600 * 24) + 1
    );
    props.AreaData(area);
    props.AreaNameData(areaName);
    props.State();
  };

  return (
    <div className="planner-date-container">
      <h1>   </h1>
      <h2> </h2>
      <div className="calendar-box">
        <input
          type="text"
          value={inputValue}
          placeholder=" "
          readOnly
          onClick={() => setShowCalendar(true)} //  
          style={{ cursor: "pointer" }}
        />
      </div>
      {showCalendar && (
        <div className="calendar-popup">
          <div
            className="calendar-popup-overlay"
            onClick={() => setShowCalendar(false)}
          ></div>
          <div className="calendar-popup-content">
            <DateRangePicker
              ranges={state}
              onChange={handleDateChange}
              editableDateInputs={true}
              moveRangeOnFirstSelection={false}
              minDate={today} //    
              months={2}
              direction="horizontal"
              rangeColors={["#3ecf8e"]}
              staticRanges={[]} //    
              inputRanges={[]} //    
            />
            <button className="confirm-button" onClick={handleConfirm}>
              
            </button>
          </div>
        </div>
      )}

      {/*   */}
      <div className="area">
        <h2> </h2>
        <select onChange={handleSelect} value={area != null && area}>
          {areaCoordinate.map((item, index) => {
            return (
              <option value={item.value} key={index}>
                {item.name}
              </option>
            );
          })}
        </select>
      </div>
      <div className="areaBtn">
        <button onClick={handleNext}></button>
      </div>
      
    </div>
  );
};

export default PlannerDate;
