import { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from 'react-router-dom';
import './Board.scss';

const Board = () => {
    const [planners, setPlanners] = useState([]);
    const [currentPage, setCurrentPage] = useState(1); //  
    const [totalPages, setTotalPages] = useState(1); //   
    const pageSize = 8; //     

    const navigate = useNavigate();

    const handlePlannerClick = (plannerID) => {
        const plannerItem = planners.find((item) => item.plannerID === plannerID);
        navigate(`destination?plannerID=${plannerID}`, { state: { plannerItem } });
    };

    const fetchPlanners = (page) => {
        axios.get(`https://www.tripplannerbn.shop/planner/board?page=${page - 1}&size=${pageSize}`)
            .then((response) => {
                setPlanners(response.data.content); //  
                setTotalPages(response.data.totalPages); //   
            })
            .catch((error) => {
                console.error("Error fetching planners:", error);
            });
    };

    const handlePageChange = (page) => {
        setCurrentPage(page);
        fetchPlanners(page);
    };

    useEffect(() => {
        fetchPlanners(currentPage);
    }, [currentPage]);

    //    
    const createPageButtons = (totalPages) => {
        const maxVisibleButtons = 5; //      
        const currentBlock = Math.floor((currentPage - 1) / maxVisibleButtons); //   
        const startPage = currentBlock * maxVisibleButtons + 1; //    
        const endPage = Math.min(startPage + maxVisibleButtons - 1, totalPages); //    

        const buttons = [];

        // '' 
        if (currentPage > 1) {
            buttons.push(
                <button key="first" onClick={() => handlePageChange(1)}></button>
            );
        }

        // '' 
        if (currentBlock > 0) {
            buttons.push(
                <button key="prev" onClick={() => handlePageChange(startPage - 1)}></button>
            );
        }

        //     
        for (let i = startPage; i <= endPage; i++) {
            buttons.push(
                <button
                    key={i}
                    className={i === currentPage ? 'active' : ''}
                    onClick={() => handlePageChange(i)}
                >
                    {i}
                </button>
            );
        }

        // '' 
        if (endPage < totalPages) {
            buttons.push(
                <button key="next" onClick={() => handlePageChange(endPage + 1)}></button>
            );
        }

        // '' 
        if (currentPage < totalPages) {
            buttons.push(
                <button key="last" onClick={() => handlePageChange(totalPages)}></button>
            );
        }

        return buttons;
    };

    return (
        <div className="board">
            <div className="board-wrapper">
                {/* <h1 className="board-title">     !</h1> */}

                <div className="planner-content">
                    {planners.map(planner => (
                        <div
                            key={planner.plannerID}
                            className="planner-item"
                            onClick={() => handlePlannerClick(planner.plannerID)}
                        >
                            <div className="planner-thumbnail">
                                <img src={planner.thumbnailImage} alt=" " />
                            </div>
                            <div className="planner-info">
                                <h3 className="planner-title">{planner.plannerTitle}</h3>
                                <p className="planner-username"> : {planner.username}</p>
                                <p className="planner-duration">
                                    {planner.day === 1
                                        ? ""
                                        : `${planner.day - 1} ${planner.day}`}
                                </p>
                                <p className="planner-created-at"> {new Date(planner.createAt).toLocaleDateString()}</p>
                            </div>
                        </div>
                    ))}
                </div>

                {/*   */}
                <div className="board-pagination">
                    {createPageButtons(totalPages)}
                </div>
            </div>
        </div>
    );
};

export default Board;
