import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './TravelCourse.scss';

//     
const regionOptions = [
    { value: '', label: '' },
    { value: '1', label: '' },
    { value: '6', label: '' },
    { value: '4', label: '' },
    { value: '2', label: '' },
    { value: '5', label: '' },
    { value: '3', label: '' },
    { value: '7', label: '' },
    { value: '8', label: '' },
    { value: '31', label: '' },
    { value: '32', label: '' },
    { value: '33', label: '' },
    { value: '34', label: '' },
    { value: '35', label: '' },
    { value: '36', label: '' },
    { value: '37', label: '' },
    { value: '38', label: '' },
    { value: '39', label: '' },
];

const hashtagOptions = [
    { value: '', label: '' },
    { value: 'C0112', label: '#' },
    { value: 'C0113', label: '#' },
    { value: 'C0114', label: '#' },
    { value: 'C0115', label: '#' },
    { value: 'C0116', label: '#' },
    { value: 'C0117', label: '#' },
];

//   
function getHashtag(category) {
    switch (category) {
        case 'C0112': return '#';
        case 'C0113': return '#';
        case 'C0114': return '#';
        case 'C0115': return '#';
        case 'C0116': return '#';
        case 'C0117': return '#';
        default: return '#';
    }
}

const TravelCourse = () => {
    //      
    const contentTypeId = 25;
    const [searchKeyword, setSearchKeyword] = useState('');
    const [regionFilter, setRegionFilter] = useState('');
    const [hashtagFilter, setHashtagFilter] = useState('');
    const [courseData, setCourseData] = useState([]);
    const [totalCount, setTotalCount] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [arrange, setArrange] = useState('');
    const [loading, setLoading] = useState(false); //   
    const pageSize = 10;  //   

    const navigate = useNavigate();

    const regionMap = {
        "1": "",
        "2": "",
        "3": "",
        "4": "",
        "5": "",
        "6": "",
        "7": "",
        "8": "",
        "31": "",
        "32": "",
        "33": "",
        "34": "",
        "35": "",
        "36": "",
        "37": "",
        "38": "",
        "39": ""
    };

    //  URL  
    const encodeUTF8 = () => {
        return encodeURIComponent(searchKeyword.trim());
    };

    //    
    const handleSearch = () => {
        setLoading(true); //      true 
        const currentEncodedData = encodeUTF8();
        const data = {
            keyword: currentEncodedData,
            pageNo: currentPage,
            hashtag: hashtagFilter,
            regionCode: regionFilter,
            arrange: arrange,
            contentTypeId: contentTypeId
        };

        axios.post('https://www.tripplannerbn.shop/api/getSearch', data, {
            headers: {
                'Content-Type': 'application/json',  // Content-Type JSON 
                withCredentials: true
                // 'Accept': 'application/json'
            }
        })
            .then((response) => {
  
                setCourseData(response.data.items.item || []); //    
                setTotalCount(response.data.totalCount);
                setLoading(false); //   false 
            })
            .catch((error) => {
                console.error('Error fetching courses:', error);
                setLoading(false); //     false 
            });
    };

    //       
    const handleCourseClick = (contentId, hashtag) => {
        setLoading(true); //  

        navigate(`/travelcourse-info?contentId=${contentId}`, {
            state: {
                hashtag,
            },
        });
    };

    //    
    const createPageButtons = (totalPages) => {
        const maxVisibleButtons = 5; //      
        const currentBlock = Math.floor((currentPage - 1) / maxVisibleButtons); //   
        const startPage = currentBlock * maxVisibleButtons + 1; //    
        const endPage = Math.min(startPage + maxVisibleButtons - 1, totalPages); //    

        const buttons = [];

        // '' 
        if (currentPage > 1) {
            buttons.push(
                <button key="first" onClick={() => handlePageChange(1)}></button>
            );
        }

        // '' 
        if (currentBlock > 0) {
            buttons.push(
                <button key="prev" onClick={() => handlePageChange(startPage - 1)}></button>
            );
        }

        //     
        for (let i = startPage; i <= endPage; i++) {
            buttons.push(
                <button
                    key={i}
                    className={i === currentPage ? 'active' : ''}
                    onClick={() => handlePageChange(i)}
                >
                    {i}
                </button>
            );
        }

        // '' 
        if (endPage < totalPages) {
            buttons.push(
                <button key="next" onClick={() => handlePageChange(endPage + 1)}></button>
            );
        }

        // '' 
        if (currentPage < totalPages) {
            buttons.push(
                <button key="last" onClick={() => handlePageChange(totalPages)}></button>
            );
        }

        return buttons;
    };

    //         (axios  )
    const handlePageChange = (pageNumber) => {
        setCurrentPage(pageNumber);  //   
    };

    //           
    useEffect(() => {
        handleSearch();
    }, [currentPage, arrange]);

    const totalPages = Math.ceil(totalCount / pageSize);

    return (

        <div className="tourist-wrapper">
            <div className="tourist-content">
                <h1 className="tourist-title">  </h1>
                <div className="tourist-header">
                    {/*   */}
                    <select
                        value={regionFilter}
                        onChange={(e) => setRegionFilter(e.target.value)}
                        className="filter-select"
                    >
                        {regionOptions.map((region) => (
                            <option key={region.value} value={region.value}>
                                {region.label}
                            </option>
                        ))}
                    </select>

                    {/*   */}
                    <select
                        value={hashtagFilter}
                        onChange={(e) => setHashtagFilter(e.target.value)}
                        className="filter-select"
                    >
                        {hashtagOptions.map((hashtag) => (
                            <option key={hashtag.value} value={hashtag.value}>
                                {hashtag.label}
                            </option>
                        ))}
                    </select>

                    {/*   */}
                    <input
                        type="text"
                        placeholder=" "
                        value={searchKeyword}
                        onChange={(e) => setSearchKeyword(e.target.value)}
                        className="search-input"
                    />
                    <button onClick={handleSearch} className="search-button"></button>
                </div>
                <div className="total-check">
                    <p className="total-count"> {totalCount} </p>

                    {/*   */}
                    <select
                        className="sort-select"
                        value={arrange}
                        onChange={(e) => setArrange(e.target.value)} //  
                    >
                        <option value=""></option>
                        <option value="O"></option>
                        <option value="Q"></option>
                        <option value="R"></option>
                    </select>

                </div>

                {/*    */}
                {loading ? (
                    <div className="loading-message"> ...</div>
                ) : (
                    <>
                        {/*    " "  */}
                        {courseData.length === 0 ? (
                            <div className="no-results"> .</div>
                        ) : (
                            <div className="travel-course-list-content">
                                {courseData.map((course) => {
                                    const regionName = regionMap[course.areacode] || '  ';
                                    const hashtag = getHashtag(course.cat2); //  

                                    return (
                                        <div key={course.contentid} className="travel-course-list" onClick={() => handleCourseClick(course.contentid, hashtag)}>
                                            {/* <Link to={`/travelcourse-info?id=${course.contentid}`}> */}
                                            {/* course.firstimage     */}
                                            {course.firstimage && (
                                                <img
                                                    src={course.firstimage}
                                                    alt={course.title}
                                                    className="travel-course-list__img"
                                                />
                                            )}
                                            <h4 className="course-title">{course.title}</h4>
                                            <div className="course-box">
                                                {/*  */}
                                                <p className="course-region">{regionName}</p>
                                                {/*   */}
                                                <p className="course-hashtag">{hashtag}</p>
                                            </div>
                                            {/* </Link> */}
                                        </div>

                                    );
                                })}
                            </div>
                        )}

                        {/*   */}
                        <div className="tourist-pagination">
                            {createPageButtons(totalPages)}
                        </div>
                    </>
                )}
            </div>
        </div>

    );
};

export default TravelCourse;
