
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './Header.scss';
import LogoImage from '../images/footPrint.png';
import axios from 'axios';
import { useState, useEffect } from "react";


const Header = () => {
    const location = useLocation();

    const [isLogin, setIsLogin] = useState(false);

    //     
    useEffect(() => {
        //       
        axios
            .post(
                "https://www.tripplannerbn.shop/api/cookie/validate",
                {},
                { withCredentials: true }
            )
            .then((response) => {
   
                //     
                setIsLogin(true);
            })
            .catch((error) => {
                console.error(" :", error);
                setIsLogin(false); //      false 
            });
    }, []);


    //   Header  
    if (location.pathname === '/planner/board/destination') {
        return null;
    }
    if (location.pathname === '/makePlanner') {
        return null;
    }

    return (
        <header className="header-wrapper">

            <Link className="header-logo" to="/">
            <div className="logo-wrapper">
                    <span className="logo-text"></span>
                    <img className="logo-icon" src={LogoImage} alt="Plane Icon" />
                </div>
            </Link>


            <div className="header-btns">
                <Link to="/tourist">
                    <button className="tourist-btn"></button>
                </Link>

                <Link to="/travelcourse">
                    <button className="travelcourse-btn"></button>
                </Link>


                {/*      */}
                {!isLogin ? (
                    <>
                        <Link to="/user/login">
                            <button className="login-btn"></button>
                        </Link>
                        <Link to="/user/join">
                            <button className="join-btn"></button>
                        </Link>
                    </>
                ) : (
                    <>
                        <Link to="/user/mypage">
                            <button className="join-btn"></button>
                        </Link>

                        <Link to="/user/logout">
                            <button className="logout-btn"></button>
                        </Link>
                    </>
                )}




            </div>

        </header>
    );
};

export default Header;
